import { TResource } from '../../api/model/Resource';
import { TSlot } from '../../api/model/Slot';

export const initialState = {
  resource: undefined,
  slots: new Map(),
  isOpen: false,
  selectedSlot: undefined,
  selectedWeek: 0,
};

type WeekObj = { [k: string]: TSlot | undefined }

export type TSlots = Map<string, WeekObj>;

type Action = {
  type: 'loadResource' | 'loadSlots' | 'open' | 'close' | 'previousWeek' | 'nextWeek' | 'today',
  value?: string | number | boolean | TSlot,
  data?: TSlots | TResource,
};

type State = {
  resource?: TResource,
  slots: TSlots,
  isOpen: boolean,
  selectedSlot?: TSlot,
  selectedWeek: number,
};

export const reducer = (state: State, action: Action) : State => {
  const { type, value, data } = action;
  const { resource, slots, selectedWeek } = state;
  
  switch (type) {
    case 'loadResource': return { ...state, resource: data as TResource };
    case 'loadSlots': return { ...state, slots: data as TSlots };
    case 'open': return { ...state, isOpen: true, selectedSlot: value as TSlot };
    case 'close': return { ...initialState, resource, slots, selectedWeek };
    case 'previousWeek': return { ...state, selectedWeek: selectedWeek - 1 };
    case 'nextWeek': return { ...state, selectedWeek: selectedWeek + 1 };
    case 'today': return { ...state, selectedWeek: 0 };
    default: return state;
  }
}