import { post, put, del, get } from "../utility/Fetch";
import { TBookingRequest } from "./model/Booking";

type TResourceInput = { resourceId: number };
type TSlotInput = TResourceInput & { slotId: number };
type TBookingInput = TSlotInput & { bookingId: number };
type TCreateBooking = TSlotInput & { body: TBookingRequest };
type TUpdateBooking = TBookingInput & { body: any };

// Bookings
export const listBookings = ({ resourceId, slotId }: TSlotInput) => get(`${resourceId}/slots/${slotId}/bookings`);
export const createBooking = ({ resourceId, slotId, body }: TCreateBooking) => post(`${resourceId}/slots/${slotId}/bookings`, body);
export const getBooking = ({ resourceId, slotId, bookingId }: TBookingInput) => get(`${resourceId}/slots/${slotId}/bookings/${bookingId}`);
export const updateBooking = ({ resourceId, slotId, bookingId, body }: TUpdateBooking) => put(`${resourceId}/slots/${slotId}/bookings/${bookingId}`, body);
export const deleteBooking = ({ resourceId, slotId, bookingId }: TBookingInput) => del(`${resourceId}/slots/${slotId}/bookings/${bookingId}`);

// Resources
export const listResources = () => get('');
export const getResource = ({ resourceId }: { resourceId: number }) => get(`${resourceId}`);

// Slots
export const listSlots = ({ resourceId }: { resourceId: number }) => get(`${resourceId}/slots`);
export const getSlot = ({ resourceId, slotId }: TSlotInput) => get(`${resourceId}/slots/${slotId}`);

