import React from 'react';
import { useForm } from "react-hook-form";
import { auth } from '../../firebase';
import { Flex } from '../Lib';
import { LoginBackground, LoginContainer, LoginInput, LoginError, LoginMainButton } from './styles';

const formConfig = { required: true };
function Login() {
  const { register, handleSubmit, errors } = useForm();
  
  // Typescript bug, la definizione corretta sarebbe { email, password }: { email: string, password: string }
  const onSubmit = ({ email, password }: any) => {
    auth.signInWithEmailAndPassword(email, password)
    .catch(error => console.log('Error Signing in with email and password'));
  }
  return (<>
    <LoginBackground>

    <LoginContainer>
      <Flex flexDirection="column">
        <h1>BiBooking</h1>
      </Flex>
      <form onSubmit={handleSubmit(onSubmit)}>
        <LoginInput name="email" ref={register(formConfig)} placeholder="Email" error={errors.email} />
        { errors.email && <LoginError>Inserisci un indirizzo email valido</LoginError>}
        <LoginInput name="password" ref={register(formConfig)} placeholder="Password" error={errors.password} />
        { errors.password && <LoginError>La password non può essere vuota</LoginError>}
        <LoginMainButton type="submit">Log in</LoginMainButton>
      </form>
    </LoginContainer>
    </LoginBackground>
  </>);
}

export default Login;
