import React, { useMemo, useCallback, useEffect, useReducer, memo } from 'react';
import { formatDate, formatTime, compareDates } from '../../utility/functions';
import Header from '../Header';
import { reducer, initialState, TSlots } from './reducer';
import { Flex, Tooltip } from '../Lib';
import CalendarDialog from './CalendarDialog';
import { WeekSelectorStyled, DayHeaderStyled, CalendarHeaderGrid, CalendarGrid, CellStyled, HourCellStyled } from './styles';
import { ChevronLeft, ChevronRight } from '../../utility/Icons';
import { TSlot } from '../../api/model/Slot';
import { listResources } from '../../api';

const DAYS = ['Lunedì', 'Martedì', 'Mercoledì', 'Giovedì', 'Venerdì', 'Sabato', 'Domenica'];
const MONTHS: { [key: string]: string } = {'01':'Gennaio','02':'Febbraio','03':'Marzo','04':'Aprile','05':'Maggio','06':'Giugno','07':'Luglio','08':'Agosto','09':'Settembre','10':'Ottobre','11':'Novembre','12':'Dicembre'};

const mockData = [
  {
    id: 0,
    starts_at: {
      datetime: '2020-10-26T15:30:00.000Z',
      timezone: 'string'
    },
    ends_at: {
      datetime: '2020-10-26T16:00:00.000Z',
      timezone: 'string'
    },
    capacity: 17,
    bookings: 15,
    promotion: {
      id: 0,
      name: 'string',
      code: 'string',
      image: 'string',
      description: 'string'
    }
  },
  {
    id: 1,
    starts_at: {
      datetime: '2020-10-26T16:00:00.000Z',
      timezone: 'string'
    },
    ends_at: {
      datetime: '2020-10-26T16:30:00.000Z',
      timezone: 'string'
    },
    capacity: 17,
    bookings: 3,
    promotion: {
      id: 1,
      name: 'string',
      code: 'string',
      image: 'string',
      description: 'string'
    }
  },
  {
    id: 2,
    starts_at: {
      datetime: '2020-10-28T13:00:00.000Z',
      timezone: 'string'
    },
    ends_at: {
      datetime: '2020-10-28T13:30:00.000Z',
      timezone: 'string'
    },
    capacity: 17,
    bookings: 10,
    promotion: {
      id: 1,
      name: 'string',
      code: 'string',
      image: 'string',
      description: 'string'
    }
  }
];

function Calendar () {
  const [state, dispatch] = useReducer(reducer, initialState);
  const { slots, selectedWeek, isOpen, selectedSlot } = state;

  const week = useMemo((day = new Date()) => {
    day.setDate(day.getDate() + (7 * selectedWeek));
    return [1,2,3,4,5,6,7].map(i => formatDate(new Date(day.setDate(day.getDate() - day.getDay() + i))));
  }, [selectedWeek]);

  const onDismiss = () => dispatch({ type: 'close' });
  const onClick = useCallback((value: TSlot) => dispatch({ type: 'open', value }), []);
  const handlePreviousWeek = () => dispatch({ type: 'previousWeek' });
  const handleNextWeek = () => dispatch({ type: 'nextWeek' });
  const handleToday = () => dispatch({ type: 'today' });

  const fetchSlots = useCallback(async () => {
    try {
      const resources = await listResources();
      if (resources instanceof Error) return;

      // TODO: scelto in automatico resource[0], mettere la risorsa corretta
      dispatch({ type: 'loadResource', data: resources[0] }); // TODO: serve salvarci la risorsa qui?

      // TODO: è possibile avere più slot_duration nella stessa settimana in seguito ad una modifica? 
      const { slot_duration } = resources[0]; // TODO
      const start_time = 6; // TODO: hardcoded
      const end_time = 24; // TODO: hardcoded
      const sumDates = (hour: number, minutes: number) => formatTime(new Date(0, 0, 0, hour, minutes, 0, 0));
      
      const data: TSlots =
        [...Array((end_time - start_time) * 60 / slot_duration)]
        .map((_, i) => i * slot_duration)
        .reduce((acc, current) => acc.set(sumDates(start_time, current), {}), new Map());
      const list = mockData; // TODO: chiamare listSlots
      // TODO: far creare un endpoint per chiedere la lista degli slot di una certa settimana
      if (!(list instanceof Error))  {
        list.forEach((d: TSlot) => {
          const date = d.starts_at.datetime.slice(0, 10); // TODO: è possibile che la start_at e ends_at siano su giorni diversi?
          const time = d.starts_at.datetime.slice(11, 16); // TODO: è possibile che la start_at e ends_at siano su giorni diversi?
          data.set(time, { ...data.get(time), [date]: d });
        });

        dispatch({ type: 'loadSlots', data });
      }
    } catch (error) {
      console.log(error)
    }
  }, []);

  useEffect(() => { fetchSlots(); }, [fetchSlots]);

  return (
    <>
      <Header>
        <WeekSelector {...{ handlePreviousWeek, handleNextWeek, handleToday }} />
      </Header>
      <CalendarHeaderGrid>
        <span/>
        {DAYS.map((d, i) => <DayHeader key={`day_header_${d}`} day={d} month={week[i].slice(5,7)} date={week[i].slice(8,10)} year={week[i].slice(0,4)} />)}
      </CalendarHeaderGrid>
      <CalendarGrid rows={slots.size}>
        {Array.from(slots).map(v => (<>
          <HourCellStyled><span>{v[0]}</span></HourCellStyled>
          {week.map(w => <Cell onClick={onClick} slot={v[1][w]} />)}
        </>))}
      </CalendarGrid>
      { selectedSlot && <CalendarDialog {...{ isOpen, onDismiss, slot: selectedSlot }} /> }
    </>
  )
}

const WeekSelector = ({ handlePreviousWeek, handleNextWeek, handleToday }:
  { handlePreviousWeek: () => void, handleNextWeek: () => void, handleToday: () => void
}) => (
  <WeekSelectorStyled>
    <button onClick={handleToday}>Oggi</button>
    <Tooltip label="Settimana precedente"><Flex onClick={handlePreviousWeek}><ChevronLeft /></Flex></Tooltip>
    <Tooltip label="Prossima settimana"><Flex onClick={handleNextWeek}><ChevronRight /></Flex></Tooltip>
  </WeekSelectorStyled>
);

const DayHeader = ({ day, month, date, year }: { day: string, month: string, date: string, year: string }) => (
  <DayHeaderStyled today={compareDates(date, month, year)}>
    <span>{day}</span>
    <span>{MONTHS[month]} {date}</span>
  </DayHeaderStyled>
);

const Cell = memo(({ onClick, slot }: { onClick: (selectedSlot: TSlot) => void, slot?: TSlot }) =>
  slot
  ? (
    <CellStyled onClick={() => onClick(slot)} percentage={Number(slot.bookings / slot.capacity * 100).toFixed(2)}>
      {slot.bookings} prenotazioni
    </CellStyled>
  ) : <CellStyled />
);


export default Calendar;