const teamId = '2';
const baseUrl = `https://api.qb.bitapp.dev/admin/teams/${teamId}/resources/`;

async function request(url: string, body?: any, method = 'GET') {
  const options = {
    method,
    headers: {
      'Content-Type': 'application/json',
      'X-Client-Key': '1aJrPT5G3xqHvT0DDrarPez7yJ5S7yF5VFCZngOR8NnoEjYtyCRNGq7yAxPGr4D6',
      'X-Client-Secret': 'client-secret'
    },
    body: JSON.stringify(body)
  };

  const res = await fetch(`${baseUrl}${url}`, options);
  const json = await res.json();

  if (res.status !== 200) return new Error(json.message);

  return json.data;
}

export const get = (url: string) => request(url);
export const post = (url: string, body: any) => request(url, body, 'POST');
export const put = (url: string, body: any) => request(url, body, 'PUT');
export const patch = (url: string, body: any) => request(url, body, 'PATCH');
export const del = (url: string) => request(url, undefined, 'DELETE');